body {
    text-align: center;
    padding: 5%;
    background-color: #111;
    color:#fff;
    font-family: Arial;
}

.meme-button{
    cursor: pointer;
    font-style: italic;
    padding: 10px;
}

.meme-item{
    text-align: left;
    display: inline-block;
    padding: 10px;
    position: relative;
    width: 150px;
    height: 150px;
    margin: 5px;
    cursor: pointer;
}

.meme-img{
    width:150px;
    height: 150px;
    border-radius: 8px;
    border:3px solid #fff;
    position: absolute;
    -o-object-fit: cover;
       object-fit: cover;
    -webkit-filter:brightness(90%);
            filter:brightness(90%);
}

.my-meme-img{
    position: relative;
    display: inline-block;
    margin: 5px;
    width: 220px;
    border-radius: 5px;
    border:3px solid white;
}

.meme-txt{
    width:150px;
    text-align: center;
    position: absolute;
    border-radius: 5px;
    bottom:0px;
    -webkit-animation-name: fade-in;
            animation-name: fade-in;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    font-size: 14px;
}

.no-txt{
    display: none;
}

.darken-img{
    background-color: gray;
    -webkit-filter:brightness(10%);
            filter:brightness(10%);
    -webkit-animation-name:darken;
            animation-name:darken;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
}

@-webkit-keyframes darken {
    from { -webkit-filter:brightness(90%); filter:brightness(90%)};
    to { -webkit-filter:brightness(50%); filter:brightness(50%)};
}

@keyframes darken {
    from { -webkit-filter:brightness(90%); filter:brightness(90%)};
    to { -webkit-filter:brightness(50%); filter:brightness(50%)};
}

@-webkit-keyframes fade-in {
    from { opacity:0};
    to { opacity:100};
}

@keyframes fade-in {
    from { opacity:0};
    to { opacity:100};
}


